
















import * as THREE from 'three'
import Vue from 'vue'
import * as ThreeSceneLoader from '../scripts/Three/ThreeSceneLoader'

export default Vue.extend({
    mounted() {
        if (this.$refs['canvas-container'] instanceof HTMLElement) {
            ThreeSceneLoader.initScene(this.$refs['canvas-container'])
        }
        if (this.$refs['dom3d-container'] instanceof HTMLElement && this.$refs['dom3d-test'] instanceof HTMLElement) {
            ThreeSceneLoader.registerCSSCoordinator(this.$refs['dom3d-container'], this.$refs['dom3d-test'], new THREE.Vector3(-350, -250, 0))
        }
        if (this.$refs['dom3d-container'] instanceof HTMLElement && this.$refs['dom3d-corner'] instanceof HTMLElement) {
            const cornerObj = ThreeSceneLoader.registerCSSCoordinator(this.$refs['dom3d-container'], this.$refs['dom3d-corner'], new THREE.Vector3(0, 20, 10))
            cornerObj.setFollowObject(ThreeSceneLoader.cornerObject, new THREE.Vector3())
            
        }
    },
    methods: {
        cornerClicked() {
            alert('corner clicked!')
        }
    }
})
