import * as THREE from 'three'
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls'
import { EffectComposer } from 'three/examples/jsm/postprocessing/EffectComposer'
import { RenderPass } from 'three/examples/jsm/postprocessing/RenderPass'
import { ShaderPass } from 'three/examples/jsm/postprocessing/ShaderPass'
import { FXAAShader  } from 'three/examples/jsm/shaders/FXAAShader'
// import { ClearPass } from 'three/examples/jsm/postprocessing/ClearPass'
// import FresnelShader from './FresnelShader'
import { loadShader } from './ShaderLoader'
import { Stats } from 'three-stats'
import { TardisCard } from './TardisCard'
import { CustomCSS3DObject, CustomCSS3DSprite, CustomCSS3DRenderer } from './CustomCSS3DRenderer'
import { DampingObjectController } from './Utils'

var container: HTMLElement
var renderer: THREE.WebGLRenderer, camera: THREE.PerspectiveCamera, controls: OrbitControls
var fxaaPass: ShaderPass
var composer: EffectComposer
var cardOutsideScene: THREE.Scene, cardInsideScene: THREE.Scene
var stats: Stats

export var cornerObject: THREE.Mesh
var cornerObjectClone: THREE.Mesh
var cornerObjectController: DampingObjectController

var cssScene: THREE.Scene
var cssRenderer: CustomCSS3DRenderer

var cursorLightGroup: THREE.Group
var cursorLight: THREE.PointLight
var cursorLightController: DampingObjectController
var mouseX: number, mouseY: number

// https://github.com/stemkoski/stemkoski.github.com/blob/master/Three.js/Camera-Texture.html
var glassBackScene: THREE.Scene
var glassFrontScene: THREE.Scene
var glassBackRenderTarget: THREE.WebGLRenderTarget
var glassBackdepthTexture: THREE.DepthTexture
var glassBackMaterial: THREE.Material
var glassFrontMaterial: THREE.ShaderMaterial

export function initScene(_container: HTMLElement) {
    container = _container
    camera = new THREE.PerspectiveCamera( 45, window.innerWidth / window.innerHeight, 1, 5000 )
    camera.position.set(0, 0, 1000)
    camera.lookAt(0, 0, 0)

    cardOutsideScene = new THREE.Scene()
    cardInsideScene = new THREE.Scene()

    renderer = new THREE.WebGLRenderer( {antialias: true} )
    // renderer.autoClear = false
    renderer.setClearColor( 0xffffff )
    renderer.setPixelRatio( window.devicePixelRatio )
    renderer.setSize(window.innerWidth, window.innerHeight)
    container.appendChild( renderer.domElement )
    controls = new OrbitControls(camera, renderer.domElement)
    renderer.domElement.style.position = 'absolute'
    renderer.domElement.style.top = '0px'
    renderer.domElement.style.left = '0px'

    // stat
    stats = new Stats()
    stats.domElement.style.zIndex = '100'
    container.appendChild( stats.domElement )

    // add objects

    // const cdt = new THREE.TextureLoader().load( './cd.jpg' )
    // const cnt = new THREE.TextureLoader().load( './cn.jpg' )
    // const crt = new THREE.TextureLoader().load( './cr.jpg' )

    // add a tardis card
    var tardisCard1 = new TardisCard(500, 700, 123, cardOutsideScene, cardInsideScene, new THREE.Vector3(0, 0, 0), new THREE.Euler())
    var contentInTardisCard1 = new THREE.Mesh(
        new THREE.BoxGeometry(200, 200, 200),
        new THREE.MeshPhysicalMaterial({
            color: 0xff00ff
        })
    )
    var contentInTardisCard2 = new THREE.Mesh(
        new THREE.BoxGeometry(200, 200, 200),
        new THREE.MeshPhysicalMaterial({
            color: 0xffff00
        })
    )
    contentInTardisCard1.position.set(30, 30, -30)
    contentInTardisCard2.position.set(-30, -30, 30)
    // scene.add(contentInTardisCard)
    var contentGroupInTardisCard1 = new THREE.Group()
    contentGroupInTardisCard1.add(contentInTardisCard1)
    contentGroupInTardisCard1.add(contentInTardisCard2)
    contentGroupInTardisCard1.position.copy(tardisCard1.cardMesh.position)
    contentGroupInTardisCard1.position.z -= 300
    tardisCard1.addVisibleObjectGroup(contentGroupInTardisCard1)

    const glassNormalTexture = new THREE.TextureLoader().load( './golf-normal.jpg' )
    // add second tardis card
    var tardisCard2 = new TardisCard(700, 500, 124, cardOutsideScene, cardInsideScene, new THREE.Vector3(650, 0, 0), new THREE.Euler())
    var contentInTardisCard3 = new THREE.Mesh(
        new THREE.SphereGeometry(100),
        new THREE.MeshPhysicalMaterial({
            metalness: 0.5,
            roughness: 0.2,
            clearcoat: 0.2,
            normalMap: glassNormalTexture
        })
    )
    var contentInTardisCard4 = new THREE.Mesh(
        new THREE.SphereGeometry(100),
        new THREE.MeshPhysicalMaterial({
            metalness: 0.2,
            roughness: 0.2,
            clearcoat: 0.5,
            normalMap: glassNormalTexture
        })
    )
    contentInTardisCard3.position.set(30, 30, -30)
    contentInTardisCard4.position.set(-30, -30, 30)
    // scene.add(contentInTardisCard)
    var contentGroupInTardisCard2 = new THREE.Group()
    contentGroupInTardisCard2.add(contentInTardisCard3)
    contentGroupInTardisCard2.add(contentInTardisCard4)
    contentGroupInTardisCard2.position.copy(tardisCard2.cardMesh.position)
    contentGroupInTardisCard2.position.z -= 300
    tardisCard2.addVisibleObjectGroup(contentGroupInTardisCard2)
    
    const hemiLight = new THREE.HemisphereLight( 0xffffff, 0x444444 )
    hemiLight.position.set( 0, 1000, 0 )

    const dirLight = new THREE.DirectionalLight( 0xffffff, 0.8 )
    dirLight.position.set( - 3000, 1000, - 1000 )

    cardInsideScene.add( hemiLight )
    cardInsideScene.add( dirLight )

    // add env
    const r = './cube1/'

    const urls = [
        r + 'px.jpg', r + 'nx.jpg',
        r + 'py.jpg', r + 'ny.jpg',
        r + 'pz.jpg', r + 'nz.jpg'
    ]

    const reflectionTextureCube = new THREE.CubeTextureLoader().load( urls )
    // reflectionTextureCube.mapping = THREE.CubeReflectionMapping

    cardOutsideScene.environment = reflectionTextureCube
    cardInsideScene.environment = reflectionTextureCube

    
    tardisCard1.addFrontWall(50)
    tardisCard2.addFrontWall(100, new THREE.MeshPhysicalMaterial({
        color: 0xa0a0a0,
        metalness: 0,
        roughness: 0,
        clearcoat: 0.5,
        reflectivity: 0.2,
        clearcoatRoughness: 0.8,
    }))
    tardisCard2.addSkybox(reflectionTextureCube)
    

    // add cursor light
    cursorLightGroup = new THREE.Group()
    cursorLight = new THREE.PointLight()
    cursorLight.intensity = 0.5
    cursorLight.decay = 1
    cursorLight.distance = 500
    cursorLight.color = new THREE.Color( 0xaaccff )
    cursorLightGroup.add(cursorLight)
    cardInsideScene.add(cursorLightGroup)
    cursorLightController = new DampingObjectController(cursorLightGroup, 0.3, 0.1)
    window.addEventListener('mousemove', e => {
        mouseX = e.clientX
        mouseY = e.clientY
    })

    cssScene = new THREE.Scene()

    const renderPass1 = new RenderPass(cardOutsideScene, camera)
    renderPass1.clear = true

    const renderPass2 = new RenderPass(cardInsideScene, camera)
    renderPass2.clearDepth = true
    renderPass2.clear = false
    

    fxaaPass = new ShaderPass( FXAAShader )
    fxaaPass.material.uniforms[ 'resolution' ].value.x = 1 / ( container.offsetWidth * window.devicePixelRatio )
    fxaaPass.material.uniforms[ 'resolution' ].value.y = 1 / ( container.offsetHeight * window.devicePixelRatio )
    
    composer = new EffectComposer(renderer)
    composer.addPass(renderPass1)
    composer.addPass(renderPass2)
	composer.addPass( fxaaPass )
    composer.renderTarget1.stencilBuffer = true
    composer.renderTarget2.stencilBuffer = true

    composer.renderToScreen = false

    // add corner object
    glassBackdepthTexture = new THREE.DepthTexture(1024, 1024)
    glassBackRenderTarget = new THREE.WebGLRenderTarget(
        container.offsetWidth * window.devicePixelRatio,
        container.offsetHeight * window.devicePixelRatio
    )
    glassBackRenderTarget.depthBuffer = true;
    glassBackRenderTarget.depthTexture = glassBackdepthTexture;
    glassBackRenderTarget.depthTexture.type = THREE.UnsignedIntType;

    glassBackScene = new THREE.Scene()
    glassFrontScene = new THREE.Scene()

    const glassDiffuseMaterial = new THREE.TextureLoader().load( './glass_cube_diffuse.png' )
    let cornerGeom = new THREE.BoxGeometry( 40, 40, 40 )
    cornerObject = new THREE.Mesh( cornerGeom, new THREE.MeshBasicMaterial({
        map: glassDiffuseMaterial,
        transparent: true,
        // depthTest: false,
        // depthWrite: true,
        // stencilWrite: true,
        // stencilRef: 999,
        // stencilZPass: THREE.ReplaceStencilOp,  // write if the depth buffer test passes
        side: THREE.BackSide
    }) )
    // cornerObject.material = new THREE.MeshDepthMaterial({
    //     side: THREE.BackSide
    // })
    cornerObject.rotation.x = Math.random() * Math.PI
    cornerObject.rotation.y = Math.random() * Math.PI
    cornerObject.rotation.z = Math.random() * Math.PI
    glassBackScene.add(cornerObject)

    var [glassFrontVS, glassFrontFS] = loadShader('GlassFrontShader')
    console.log(glassBackRenderTarget.depthTexture)
    var glassFrontUniforms = 
    {
        "resolution": { type: "v", value: new THREE.Vector2(
            container.offsetWidth * window.devicePixelRatio,
            container.offsetHeight * window.devicePixelRatio
        ) },
        "tBackground":    { type: "t", value: glassBackRenderTarget.texture },
        "tBackDepth": { type: "t", value: glassBackRenderTarget.depthTexture },

    }
    glassFrontMaterial = new THREE.ShaderMaterial( 
        {
            vertexShader:   glassFrontVS,
            fragmentShader: glassFrontFS,
            uniforms: glassFrontUniforms,
            depthTest: false,
            // stencilWrite: true,
            // stencilRef: 999,
            // stencilZPass: THREE.ReplaceStencilOp,  // write if the depth buffer test passes
            side: THREE.FrontSide
        }
    )
    cornerObjectClone = cornerObject.clone()
    cornerObjectClone.material = glassFrontMaterial
    glassFrontScene.add(cornerObjectClone)

    cornerObjectController = new DampingObjectController(cornerObject, 0.5, 0.1)
    cornerObjectController.addChildObj(cornerObjectClone)
    
    glassBackScene.background = composer.renderTarget2.texture
    glassFrontScene.background = glassBackRenderTarget.texture


    window.addEventListener( 'resize', onWindowResize )
    animate()
}

export function registerCSSCoordinator(htmlContainer: HTMLElement, htmlElem: HTMLElement, position: THREE.Vector3 = new THREE.Vector3()): CustomCSS3DObject {
    if (cssRenderer == undefined) {
        cssRenderer = new CustomCSS3DRenderer(htmlContainer)
        cssRenderer.setSize( window.innerWidth, window.innerHeight )
    }
    // controls = new OrbitControls(camera, cssRenderer.domElement)
    const object = new CustomCSS3DObject( htmlElem )
    object.position.copy(position)
    cssScene.add(object)
    return object
    // cssCoordinators.push(new ThreeCSSCoordinator(htmlContainer, htmlCamera, htmlElem, camera, position))
}

function getRaycastPos(posX: number, posY: number) {
    let x = (2.0 * posX) / window.innerWidth - 1.0
    let y = 1.0 - (2.0 * posY) / window.innerHeight
    // let z = 1.0
    // let ray_nds = new THREE.Vector3(x, y, z)
    let ray = new THREE.Vector3(x, y, 0.9975)
    ray.unproject(camera)
    return ray
}

function updatecornerObject() {
    let targetPos = getRaycastPos(200, 200)
    // let lastPos = cornerObject.position
    cornerObjectController.update(targetPos)
    // let currentPath = targetPos.sub(lastPos)
    // cornerObjectVelocity.multiplyScalar(0.3)
    // cornerObjectVelocity.add(currentPath.multiplyScalar(damp))
    // cornerObject.position.add(cornerObjectVelocity)
}

function updateCursorLight() {
    if (!isNaN(mouseX) && !isNaN(mouseY)) {
        let targetPos = getRaycastPos(mouseX, mouseY)
        let camPosZ = camera.position.z
        let targetPosVec = new THREE.Vector3().copy(targetPos)
        targetPosVec = targetPosVec.sub(camera.position)
        let newz = -80
        let x = (newz - camPosZ) / targetPosVec.z
        targetPos.copy(camera.position)
        targetPosVec.multiplyScalar(x)
        targetPos.add(targetPosVec)
        cursorLightController.update(targetPos)
    }
}

function onWindowResize() {

    camera.aspect = window.innerWidth / window.innerHeight
    camera.updateProjectionMatrix()

    fxaaPass.material.uniforms[ 'resolution' ].value.x = 1 / ( container.offsetWidth * window.devicePixelRatio )
    fxaaPass.material.uniforms[ 'resolution' ].value.y = 1 / ( container.offsetHeight * window.devicePixelRatio )
    glassFrontMaterial.uniforms[ 'resolution' ].value.x = container.offsetWidth * window.devicePixelRatio
    glassFrontMaterial.uniforms[ 'resolution' ].value.y = container.offsetHeight * window.devicePixelRatio
    renderer.setSize( window.innerWidth, window.innerHeight )
    composer.setSize( window.innerWidth, window.innerHeight )
    if (cssRenderer) {
        cssRenderer.setSize( window.innerWidth, window.innerHeight )
    }

}

function animate() {
    requestAnimationFrame( animate )
    updatecornerObject()
    updateCursorLight()
    render()
    controls.update()
    stats.update()

    // https://github.com/mrdoob/three.js/blob/master/examples/js/renderers/CustomCSS3DRenderer.js
	// camera.updateMatrixWorld();
    // camera.updateMatrixWorld()
    // const fov = camera.projectionMatrix.elements[ 5 ] * window.innerHeight;
    // const style = 'translateZ(' + fov + 'px)' + getCameraCSSMatrix( camera.matrixWorldInverse ) // + 'translate(' + window.innerWidth/2 + 'px,' + window.innerHeight/2 + 'px)';
    // let cameraCSSMatrix = getCameraCSSMatrix( camera.matrixWorldInverse.toArray() )
    // let style = 'translate(' + window.innerWidth/2 + 'px,' + window.innerHeight/2 + 'px)' + cameraCSSMatrix;
    // cssCoordinators.forEach(coordinator => {
    //     coordinator.update(fov, style)
    // })

}

function render()
{
    composer.render()
    renderer.clearDepth()
    renderer.setRenderTarget(glassBackRenderTarget)
    renderer.render(glassBackScene, camera)
    renderer.setRenderTarget(null)
    renderer.render(glassFrontScene, camera)
    if (cssRenderer) {
        cssRenderer.render(cssScene, camera)
    }
}